.pokemon-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  width: 40%;
  margin-left: 30%;
  margin-top: 5%;
  border: 1px solid black;
  border-radius: 5px;
}

.pokemon-container button {
  margin: 5px;
  padding: 10px;
  background-color:rgb(255,206,49);
  border-radius: 10px;
}