.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid black;
  border-radius: 5px;
  width: 25%;
  height: 250px;
  margin-left: 37.5%;
  margin-top: 5%;
  justify-content: space-evenly;
  background-color: white;
}

.form-container form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 50%;
}

.form-container input {
  margin-left: 8px;
}

.form-container div {
  display: flex;
  flex-direction: column;
  align-items: center;
}